header .change-theme-btn
{
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: transparent;
  border: none;
  border-radius: 50%;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  margin-left: auto;
  position: relative;
  font-family: Arial;
  background: transparent;
}
header .change-theme-btn::after, header .change-theme-btn::before
{
    --scale: 0;
    position: absolute;
    bottom: -1.8rem;
    left: 50%;
    -webkit-transform: translateX(-50%) translateY(0) scale(var(--scale));
            transform: translateX(-50%) translateY(0) scale(var(--scale));
    -webkit-transform: translateX(-50%) translateY(var(--translate-y, 0)) scale(var(--scale));
            transform: translateX(-50%) translateY(var(--translate-y, 0)) scale(var(--scale));
    -webkit-transform-origin: top center;
            transform-origin: top center;
    transition: all 0.15s;
}
header .change-theme-btn::before
{
    --translate-y: calc(100% - 10px);

    content: attr(tooltip);
    color: white;
    text-align: center;
    max-width: 150px;
    width: -webkit-max-content;
    width: max-content;
    padding: 5px;
    border-radius: 5px;
    background-color: rgb(70, 70, 70);
    font-size: 15px;
}
header .change-theme-btn::after
{
    --translate-y: -10px;

    content: "";
    border: solid 10px transparent;
    border-bottom-color: rgb(70, 70, 70);
    -webkit-transform-origin: bottom center;
            transform-origin: bottom center;
}
header .change-theme-btn:hover::before, header .change-theme-btn:hover:after
{
    --scale: 1;
}
