.contact-card-container
{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
    margin: 30px auto auto auto;
}
.contact-card
{
    display: flex;
    flex-direction:  column;
    align-items: center;
    justify-content: center;
    font-family: Raleway, Arial;
    font-weight: normal;
    user-select: none;
}
.contact-card-link
{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    padding: 5px 5px;
}
.contact-card-content
{
    margin-left: 10px;
    user-select: text;
    cursor: text;
}
.contact-card .contact-card-link img
{
    width: 30px;
    height: auto;
    margin: auto 10px auto 0;
}