::selection
{
  background-color: cornflowerblue;
}
::-moz-selection
{
  background-color: cornflowerblue;
}
::-webkit-scrollbar
{
  position: fixed;
  background: none;
  width: 10px;
}
::-webkit-scrollbar-button
{
  display: none;
}
::-webkit-scrollbar-thumb
{
  background: cornflowerblue;
}

*
{
  margin: 0;
  padding: 0;
  scroll-behavior: smooth;
  box-sizing: border-box;
}

html, body, #root, .app
{
  width: 100%;
  height: 100%;
  min-width: 300px;
}

header
{
  width: 100%;
  height: fit-content;
  background-color: rgb(255, 255, 255);
  display: flex;
  justify-content: space-between;
  align-items: center;
  user-select: none;
  box-shadow: 0px 0px 5px rgb(51, 51, 51);
  position: relative;
  z-index: 1;
}

header .home-link
{
  cursor: pointer;
  font-family: Raleway;
  font-weight: normal;
  font-size: 25px;
  display: flex;
  align-items: center;
  padding: 5px 0px;
  margin-right: auto;
  margin-left: 15px;
  border-bottom: 3px solid transparent;
  transition: all 0.2s;
}
header .home-link .logo-txt
{
  color: cornflowerblue;
  margin-left: 15px;
}
header .home-link img
{
  height: 60px;
  width: auto;
  margin: 0 10px;
}
header .nav .nav-link-list
{
  display: flex;
  flex-direction: row;
  align-items: center;
}
header .nav .nav-link-list li a
{
  font-family: Raleway, Arial;
  font-weight: lighter;
  font-size: 25px;
  padding: 5px 10px;
  margin: auto 10px;
}
header .change-theme-btn img
{
  height: 30px;
  width: 30px;
  transition: all 0.25s;
  margin: auto;
}
header .change-theme-btn
{
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: transparent;
  border: none;
  border-radius: 50%;
  width: fit-content;
  height: fit-content;
  margin-left: auto;
  position: relative;
  font-family: Arial;
  background: transparent;
}
header .change-theme-btn::after, header .change-theme-btn::before
{
    --scale: 0;
    position: absolute;
    bottom: -1.8rem;
    left: 50%;
    transform: translateX(-50%) translateY(var(--translate-y, 0)) scale(var(--scale));
    transform-origin: top center;
    transition: all 0.15s;
}
header .change-theme-btn::before
{
    --translate-y: calc(100% - 10px);

    content: attr(tooltip);
    color: white;
    text-align: center;
    max-width: 150px;
    width: max-content;
    padding: 5px;
    border-radius: 5px;
    background-color: rgb(70, 70, 70);
    font-size: 15px;
}
header .change-theme-btn::after
{
    --translate-y: -10px;

    content: "";
    border: solid 10px transparent;
    border-bottom-color: rgb(70, 70, 70);
    transform-origin: bottom center;
}
header .change-theme-btn:hover::before, header .change-theme-btn:hover:after
{
    --scale: 1;
}
header .resp-nav-btn
{
  background: transparent;
  cursor: pointer;
  border: none;
  margin: auto;
  display: none;
}
header .resp-nav-btn img
{
  height: 30px;
  width: auto;
}
header .resp-nav
{
  position: absolute;
  top: 102%;
  left: 0;
  width: 100%;
  padding: 10px 0;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  display: none;
  z-index: 1;
}
header .resp-nav a
{
  font-family: Raleway, Arial;
  font-weight: lighter;
  font-size: 20px;
  border-radius: 25px;
  padding: 5px 10px;
  margin: 10px auto;
}

.banner
{
  width: 100%;
  height: 90%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-image: url(./images/landscape.png);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  background-blend-mode: multiply;
  background-attachment: fixed;
  background-color: rgba(0, 77, 150, 0.3);
  user-select: none;
}
.banner-title, .banner-utitle
{
  color: white;
  font-family: Raleway, Arial;
  font-weight: normal;
  text-align: center;
  max-width: 90%;
}
.banner-title
{
  font-size: 40px;
  margin-bottom: 20px;
}
.banner-utitle
{
  font-size: 30px;
}

.separator
{
  background: transparent;
  width: 100%;
  height: 1px;
}

.section-container
{
  height: fit-content;
  padding: 3% 0;
  box-shadow: 1px 0 6px -2px rgb(32, 32, 32);
}
.section
{
  width: 100%;
  min-width: 275px;
  height: fit-content;
  display: flex;
  flex-direction: row;
  padding: 0 0 10px 0;
}
.section:not(:first-of-type)
{
  margin: 4% auto;
}
.section:first-of-type
{
  margin: 10px auto 5% auto;
}
.section-title
{
  font-size: 25px;
  font-family: Raleway, Arial;
  font-weight: normal;
  text-align: center;
  user-select: none;
  margin: 0 auto 15px auto;
}
.section-title-desc
{
  font-size: 18px;
  font-family: Raleway, Arial;
  font-weight: normal;
  text-align: center;
  color: rgb(125, 125, 125);
  user-select: none;
  margin: 0 auto 15px auto;
  max-width: 90%;
}
.section-svg
{
  width: 50%;
  height: fit-content;
  margin: auto;
}
.section-textarea
{
  width: 50%;
  display: flex;
  align-items: center;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
}
.section-cardarea
{
  display: flex;
  align-items: center;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  width: 50%;
}
.expand-hidden-btn
{
  position: relative;
  width: 100%;
  cursor: pointer;
  border: none;
  padding: 5px 0;
  margin: 10px auto 0 auto;
  font-size: 18px;
}

.backtop-btn
{
  position: fixed;
  bottom: 50px;
  left: 20px;
  z-index: 1;
  background: transparent;
  border: none;
  border-radius: 50%;
  cursor: pointer;
  display: none;
  user-select: none;
}
.backtop-btn img
{
  width: auto;
  height: 50px;
}

footer
{
  width: 100%;
  height: fit-content;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  bottom: 0;
  left: 0;
  box-shadow: 0px 0px 5px rgb(51, 51, 51);
}
footer .footer-links-container
{
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  flex-wrap: wrap;
  font-family: Raleway, Arial;
  font-weight: normal;
  user-select: none;
  width: 98%;
  color: gray;
}
footer .footer-links-container .footer-links-box
{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin: 0 auto auto auto;
  width: 200px;
  color: gray;
}
footer .footer-links-container .footer-links-box .footer-links-box-title
{
  text-align: center;
  font-size: 16px;
  margin: 5px auto 10px auto;
}
footer .footer-links-container .footer-links-box ul li
{
  text-align: center;
  font-size: 14px;
  margin: 5px auto;
  color: gray;
}
footer .footer-links-container .footer-links-box ul li a
{
  color: gray;
}
footer .footer-links-container .footer-links-box ul li a:hover
{
  border-bottom: thin solid;
}
footer .address-line1, footer .address-line2
{
  max-width: 100%;
  user-select: text;
  font-size: 15px;
}

.pagenf-bg
{
  display: flex;
  flex-direction: column;
  align-items: center;
  user-select: none;
}
.pagenf-title
{
  text-align: center;
  margin: 3% auto 0 auto;
  font-size: 30px;
  font-family: Raleway, Arial;
}
.pagenf-img
{
  width: 40%;
  min-width: 275px;
  height: auto;
  margin: auto;
}
.pagenf-link
{
  text-align: center;
  margin: 0 auto 3% auto;
  color: cornflowerblue;
  font-size: 25px;
  font-family: Raleway, Arial;
}

.copyright
{
  color: rgb(130, 130, 130);
  font-family: Raleway, Arial;
  font-size: 16px;
  font-weight: normal;
  text-align: center;
  margin: 12.5px auto 5px auto;
  user-select: none;
}

.dev-by
{
  font-size: 16px;
  font-family: "Montserrat Alternates";
  font-weight: normal;
  cursor: default;
  padding: 5px 0px;
  text-align: center;
  color: gray;
  user-select: none;
  text-align: center;
}
.kosmicgradient
{
  background: -webkit-linear-gradient(to , rgb(94, 0, 94), rgb(161, 0, 161));
  background: -o-linear-gradient(to right, rgb(94, 0, 94), rgb(161, 0, 161));
  background: -moz-linear-gradient(to right, rgb(94, 0, 94), rgb(161, 0, 161));
  background: linear-gradient(to right, rgb(94, 0, 94), rgb(161, 0, 161));
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  font-size: 16px;
  font-family: "Montserrat Alternates", Arial;
  font-weight: bold;
  cursor: pointer;
}

@media screen and (min-width: 1300px)
{
  header .change-theme-btn
  {
    margin-right: 100px;
  }
  .about-text
  {
    font-size: 20px;
  }
  .expand-hidden-btn
  {
    display: none;
  }
}
@media screen and (min-width: 900px) and (max-width: 1299px)
{
  header .change-theme-btn
  {
    margin-right: 100px;
  }
  .about-text
  {
    font-size: 20px;
  }
  .expand-hidden-btn
  {
    display: none;
  }
}
@media screen and (min-width: 600px) and (max-width: 899px)
{
  header
  {
    justify-content: center;
  }
  header .home-link
  {
    margin-right: auto;
    font-size: 22px;
  }
  header .home-link img
  {
    height: 55px;
  }
  header .change-theme-btn
  {
    margin-right: 20px;
  }
  header .change-theme-btn::before, header .change-theme-btn::after
  {
    display: none;
  }
  .about-text
  {
    font-size: 20px;
  }
  header .nav
  {
    display: none;
  }
  .expand-hidden-btn
  {
    display: none;
  }
  .section-svg
  {
    display: none;
  }
  .section-textarea, .section-cardarea
  {
    width: 100%;
  }
  .section-textarea .about-text
  {
    max-width: 80%;
  }
}
@media screen and (min-width: 0px) and (max-width: 599px)
{
  header
  {
    justify-content: center;
    height: 8%;
  }
  header .home-link
  {
    margin-left: 5px;
    margin-right: auto;
    border: none;
  }
  header .home-link img
  {
    height: 50px;
  }
  header .home-link .logo-txt
  {
    display: none;
  }
  header .change-theme-btn
  {
    margin-right: 20px;
  }
  header .change-theme-btn::before, header .change-theme-btn::after
  {
    display: none;
  }
  header .nav
  {
    display: none;
  }
  .section
  {
    flex-direction: column;
  }
  .section-textarea
  {
    max-height: 400px;
    overflow-y: hidden;
    width: 100%;
  }
  .section-cardarea
  {
    width: 100%;
  }
    .section-svg
  {
    display: none;
  }
  .section-textarea .about-text
  {
    max-width: 80%;
  }
  .contact-card-link
  {
    font-size: 15px;
  }
}

a, button, input, ul, li
{
  text-decoration: none;
  outline: none;
  background-color: transparent;
  list-style: none;
}

@import url('https://fonts.googleapis.com/css2?family=Raleway:wght@300;400;500&display=swap');