.light-background-full
{
    transition: 0.2s;
    background: white;
}
.dark-background-full
{
    transition: 0.2s;
    background: black;
}
.light-background
{
    transition: 0.2s;
    background: rgb(240, 240, 240);
}
.dark-background
{
    transition: 0.2s;
    background: rgb(10, 10, 10);
}

.light-text
{
    transition: 0.2s;
    color: white;
}
.dark-text
{
    transition: 0.2s;
    color: black;
}

.no-invert
{
    transition: 0.2s;
    filter: invert(0);
}
.invert
{
    transition: 0.2s;
    filter: invert(100%);
}

.dark-backhover:hover
{
    transition: 0.2s;
    background-color: rgba(255, 255, 255, 0.2);
}
.light-backhover:hover
{
    transition: 0.2s;
    background-color: rgba(0, 0, 0, 0.1);
}

.svg-light-back
{
    transition: 0.2s;
    fill: #fff;
}
.svg-dark-back
{
    transition: 0.2s;
    fill: rgb(10, 10, 10);
}
.svg-lgray-back
{
    transition: 0.2s;
    fill: #e4e4e4;
}
.svg-dgray-back
{
    transition: 0.2s;
    fill: #666;
}

.svg-hide
{
    display: none;
}