.services-card-container
{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
}
.service-card
{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    margin: 10px auto;
    padding: 10px 0;
    cursor: pointer;
    width: 275px;
    font-family: Raleway, Arial;
    font-weight: normal;
    user-select: none;
}
.service-card .service-title
{
    text-align: center;
    margin: 0 auto 15px auto;
}
.service-card img
{
    width: 85px;
    height: 85px;
}
.service-card .service-desc
{
    text-align: center;
    max-width: 88%;
    margin: 15px auto 0 auto;
}